body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.content {
    padding-top: 60px;
    padding-left: 60px;
}

.homeMain {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
}

.homeMain h1 {
    color: aliceblue;
    font-size: 75px;
}

.heroImage {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.heroImage img{
    padding-top: 60px;
    max-width: auto;
    max-height: auto;
}

.emailSignUp {
    width: 333px;
	height:200px;
	margin:10px;
    padding:10px;
    background:#de00df;
    border-radius: 15px 15px 15px 15px;
}

.whatWeDo {
    padding: 50px;
    color: aliceblue;
}

.whatWeDoText {
    padding-bottom: 50px;
}

.paraText p{
    fontSize: 24px;
    padding: 30px;
}




.displayImageBox2 {
    float: right;
}

.ventureProjects {
    padding-top: 100px;
}

.projectDetails {
    text-align: center;
}

.projectDetailsBox {
    width: 220px;
	height:100px;
	margin:10px;
    padding-right:30px;
    background:aliceblue;
    border-radius: 15px 15px 15px 15px;
    color: #de00df;
}

